<template><section><h1>PolylineCollection</h1>
<p>The <code v-pre>vc-collection-primitive-polyline</code> component is used to load a renderable collection of polylines.</p>
<h2>Example</h2>
<h3>Load PolylineCollection with <code v-pre>vc-collection-primitive-polyline</code></h3>
<h4>Preview</h4>
<doc-preview>
